@use "global" as *;

.contact {
  background-color: $gray;
  padding-top: rem(67);
  padding-bottom: rem(64);
  @include mq(md) {
    padding-top: rem(147);
    padding-bottom: rem(146);
  }
}
.contact__title {
  text-align: center;
  position: relative;
  @include mq(md) {
    padding-left: rem(10);
  }
}
.contact__title::before {
  content: attr(data-en-contact);
  position: absolute;
  top: rem(-50);
  left: 50%;
  transform: translateX(-50%);
  color: $yellow;
  opacity: 0.2;
  font-family: $second-font-family;
  font-size: rem(50);
  font-weight: $normal;
  line-height: lh(72, 50);
  letter-spacing: rem(2);
  @include mq(md) {
    font-size: rem(110);
    top: rem(-91);
    left: rem(219);
  }
}
.contact__body {
  margin-top: rem(20);
  @include mq(md) {
    margin-top: rem(22);
  }
}
.contact__text {
  font-size: rem(16);
  font-weight: $normal;
  line-height: lh(23, 16);
  letter-spacing: rem(2);
  color: $white;
  @include mq(md) {
    text-align: center;
  }
}
.contact__button {
  margin-top: rem(45);
  text-align: center;
  @include mq(md) {
    margin-top: rem(60);
  }
}